import React, {useRef} from "react";
import {Search, X} from "lucide-react";

interface SearchBarProps {
	searchTerm: string;
	onSearchChange: (e: React.ChangeEvent<HTMLInputElement>) => void;
	onSearch: (e: React.FormEvent) => void;
	onClear?: () => void;
	isLoading?: boolean;
}

const SearchBar: React.FC<SearchBarProps> = ({
	searchTerm,
	onSearchChange,
	onSearch,
	onClear,
	isLoading = false,
}) => {
	// Ref para mantener el foco despu√©s de limpiar
	const inputRef = useRef<HTMLInputElement>(null);

	// Manejar limpieza de la b√∫squeda y restaurar el foco
	const handleClear = () => {
		if (onClear) {
			onClear();
			// Restaurar el foco al input despu√©s de limpiar
			setTimeout(() => {
				if (inputRef.current) {
					inputRef.current.focus();
				}
			}, 0);
		}
	};

	return (
		<div className="mb-8">
			<form onSubmit={onSearch} className="relative">
				<input
					ref={inputRef}
					type="text"
					placeholder="Buscar productos..."
					className="w-full py-3 px-4 pr-16 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-primary-300 focus:border-transparent"
					value={searchTerm}
					onChange={onSearchChange}
					disabled={isLoading}
					autoComplete="off"
				/>

				{/* Bot√≥n para limpiar la b√∫squeda, solo visible si hay texto */}
				{searchTerm && (
					<button
						type="button"
						onClick={handleClear}
						className="absolute right-12 top-3 text-gray-400 hover:text-gray-600 focus:outline-none"
						aria-label="Limpiar b√∫squeda"
						disabled={isLoading}
					>
						<X size={20} />
					</button>
				)}

				<button
					type="submit"
					className="absolute right-3 top-3 text-gray-400 hover:text-primary-600 focus:outline-none flex items-center justify-center h-6 w-6"
					aria-label="Buscar"
					disabled={isLoading}
				>
					{isLoading ? (
						<div className="animate-spin rounded-full h-5 w-5 border-t-2 border-b-2 border-primary-600"></div>
					) : (
						<Search size={20} className="hover:text-primary-600" />
					)}
				</button>
			</form>

			{/* Texto instructivo para indicar al usuario que debe presionar Enter o hacer clic en la lupa */}
			<p className="mt-1 text-xs text-gray-500 text-right">
				Presiona Enter o haz clic en la lupa para buscar
			</p>
		</div>
	);
};

export default React.memo(SearchBar);
