import {useState, useCallback} from "react";

export const useProductSearch = () => {
	const [searchTerm, setSearchTerm] = useState("");
	const [isSearching, setIsSearching] = useState(false);

	// Manejar cambios en el campo de b√∫squeda (sin actualizar URL autom√°ticamente)
	const handleSearchChange = useCallback(
		(e: React.ChangeEvent<HTMLInputElement>) => {
			const value = e.target.value;
			setSearchTerm(value);
		},
		[]
	);

	// Funci√≥n para establecer el t√©rmino de b√∫squeda externamente (desde URL)
	const setSearchTermExternal = useCallback((term: string) => {
		setSearchTerm(term);
	}, []);

	// Funci√≥n para manejar env√≠o del formulario
	const handleSearchSubmit = useCallback(
		(onSearchSubmit: (searchTerm: string) => void) => (e: React.FormEvent) => {
			e.preventDefault();
			setIsSearching(true);
			
			// Llamar a la funci√≥n proporcionada para manejar la b√∫squeda
			onSearchSubmit(searchTerm);
			
			// Dar tiempo para que la b√∫squeda se procese
			setTimeout(() => {
				setIsSearching(false);
			}, 300);
		},
		[searchTerm]
	);

	// Limpiar la b√∫squeda
	const clearSearch = useCallback((onClearSearch: () => void) => {
		setSearchTerm("");
		onClearSearch();
	}, []);

	return {
		searchTerm,
		isSearching,
		handleSearchChange,
		handleSearchSubmit,
		clearSearch,
		setSearchTermExternal,
	};
};

export default useProductSearch;